<template>
    <div data-v-db4ce4e8="" class="page">
        <div data-v-152ab910="" data-v-db4ce4e8="" class="sub-header">
            <div data-v-152ab910="" class="back" @click="$router.go(-1)"></div>
            <div data-v-152ab910="" class="title">我的收藏</div><!----></div>
        <div data-v-db4ce4e8="" class="main" v-show="userFavs.length>0">
            <div data-v-db4ce4e8="" class="goods-list" v-for="(item,index) in userFavs" :key="index">
                <div data-v-db4ce4e8="" class="image"><img data-v-db4ce4e8=""
                                                               :src="item.image" alt="">
                </div>
                <div data-v-db4ce4e8="" class="title">{{item.title}}</div>
                <div data-v-db4ce4e8="" class="price">¥{{item.price}}</div>
                <div data-v-db4ce4e8="" class="btn-wrap">
                    <div data-v-db4ce4e8="" class="btn" @click="$router.push('/goods/details/item?gid='+item.gid)">购买</div>
                    <div data-v-db4ce4e8="" class="btn" @click="deleteFav(index,item.fid)">删除</div>
                </div>
            </div>

        </div>
        <div data-v-db4ce4e8=""  v-show="userFavs.length<=0" class="no-data" >您还没有收藏商品！</div>
    </div>
</template>

<script>
    import {mapActions,mapState} from "vuex"
    import vue from "vue"
    import { Dialog } from 'vant';
    import UpRefresh from '../../../assets/js/libs/uprefresh';
    vue.set(Dialog);
    export default {
        mounted(){
            document.title=this.$route.meta.title;
        },
        created(){
            //安全认证
            this.$utils.safeUser(this);
            //创建分页实例
            this.pullUp = new UpRefresh();
            //用户收藏内容
            this.getUserFav({page:1,success:(pageNum)=>{
                    //分页功能
                    this.pullUp.init({"curPage": 1, "maxPage": parseInt(pageNum), "offsetBottom": 100}, (page) => {
                        this.getUserFavPage({page: page});
                    });
                }})
        },
        computed:{
            ...mapState({
                userFavs:state=>state.goods.userFavs
            })
        },
        methods:{
            ...mapActions({
                //用户收藏内容
                getUserFav:"goods/getUserFav",
                //删除收藏
                getDelUserFav:"goods/getDelUserFav",
                //商品收藏分页
                getUserFavPage:"goods/getUserFavPage"
            }),
            //删除收藏
            deleteFav(index,fid){
                Dialog.confirm({
                    title: '',
                    message: '确认要删除吗?',
                })
                    .then(() => {
                        // on confirm
                        this.getDelUserFav({index:index,fid:fid})
                    })
                    .catch(() => {
                        // on cancel
                    });

            }
        },
        beforeDestroy() {
            this.pullUp.uneventSrcoll();
        },
    }
</script>
<style scoped>
    .page {
        width: 100%;
        min-height: 100vh;
        background-color: #FFFFFF;
    }
    .sub-header {
        width: 100%;
        height: 1rem;
        background-color: #FFFFFF;
        display: flex;
        display: -webkit-flex;
        align-items: center;
        -webkit-align-items: center;
        border-bottom: 1px solid #EFEFEF;
        position: fixed;
        z-index: 10;
        left: 0;
        top: 0;
        justify-content: space-between;
        padding: 0px 0.1rem;
        box-sizing: border-box
    }

    .sub-header .back {
        width: 0.8rem;
        height: 0.8rem;
        background-image: url("../../../assets/images/home/goods/back.png");
        background-size: 100%;
        background-repeat: no-repeat;
        background-position: center;
    }

    .sub-header .title {
        width: 79%;
        height: auto;
        font-size: 0.32rem;
        text-align: center;
        position: absolute;
        z-index: 1;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%)
    }

    .sub-header .right-btn {
        width: auto;
        height: auto;
        font-size: 0.32rem;
    }
    .main {
        width: 100%;
        padding-top: 1.2rem;
        display: flex;
        display: -webkit-flex;
        justify-content: space-between;
        -webkit-justify-content: space-between;
        box-sizing: border-box;
        padding-left: 2%;
        padding-right: 2%;
        flex-wrap: wrap;
        -webkit-flex-wrap: wrap;
    }

    .main .goods-list {
        width: 48%;
        height: 5.8rem;
        margin-bottom: 0.3rem;
        overflow: hidden;
    }

    .main .goods-list .image {
        width: 3.36rem;
        height: 3.35rem;
        margin: 0 auto;
    }

    .main .goods-list .image img {
        width: 100%;
        height: 100%;
        border: 0 none;
    }

    .main .goods-list .title {
        width: 100%;
        height: 0.8rem;
        overflow: hidden;
        font-size: 0.28rem;
        margin-top: 0.2rem;
    }

    .main .goods-list .price {
        font-size: 0.28rem;
        color: #F93036;
        margin-top: 0.2rem;
    }

    .main .goods-list .btn-wrap {
        width: 100%;
        display: flex;
        display: -webkit-flex;
        justify-content: space-between;
        -webkit-justify-content: space-between;
        margin-top: 0.2rem;
    }

    .main .goods-list .btn-wrap .btn {
        width: 1.04rem;
        height: 0.52rem;
        border: 1px solid #F93036;
        font-size: 0.28rem;
        color: #F93036;
        border-radius: 4px;
        text-align: center;
        line-height: 0.52rem;
    }
</style>